<template>
  <div>

  <nav class="navbar navbar-expand-lg navbar-light bg-light">
     <a href="#">BITPLAY</a>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button> 
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav ml-auto">
        <li class="nav-item active">
          <router-link class="nav-link" :to="`/store/productIndex`">  </router-link> 
        </li>
        <li 
          class="nav-item active" 
          v-if="$router.history.current['path'] !== '/store/checkOut'">
          <a class="nav-link" @click="openModal">  </a> 
        </li>
        <li 
          class="nav-item"
          v-if="$router.history.current['path'] !== '/store/checkOut'">
           <a  class="nav-link" href="https://bitplayinc.com/review-2/?lang=zh-hant"> </a> 
        </li>
        <li class="nav-item">
         <router-link class="nav-link" :to="`/login`"></router-link>
        </li>
         <!-- <li class="nav-item" 
          v-if="$router.history.current['path'] !== '/store/checkOut'">
           <router-link :to="`/store/checkOut`"></router-link>    
        </li> -->
                                                                

      </ul>
    </div>
  </nav>
  <CartModal></CartModal>


    
  </div>
</template>

<script>
import CartModal from "../views/CartModal"
import $ from 'jquery';
export default {
  data(){
    return{}
  },
  name: 'Navbar',
  methods:{
    signout(){
          //   API  api  api  
        const api = `${process.env.APIPATH}/logout`;
        const vm = this;
        this.$http.post(api).then(response => { //   post   vm.user
        console.log(response.data);
        if (response.data.success){ //  :   
            vm.$router.push('/login')  //   signin
        }
        });
    },
     openModal(){                          //   (  , item( item) )
      this.getCart();
      $('#cartModal').modal('show')              // bs  modal  methods
    },
     getCart(){
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;   //   id 
        vm.isLoading = true;
      this.$http.get(url).then((response) => {
          vm.isLoading = false;
          vm.cart = response.data.data;
          console.log(response.data.data.carts);
      });
      },  



  },
 
  created(){
    this.getCart();
  },
  components:{
    CartModal
  }
}
</script>

<style scoped>
 a{   
   color:rgb(116, 108, 108) !important;
 }

 .modal-backdrop.show{
   opacity: 0;
 }
</style>


